import topBox from '@/components/mbbo/topBox/topBox';
import uniCountDown from '@/components/common/uni-count-down/uni-count-down.vue';
import fixedBgBtn from '@/components/common/button/fixedBgBtn.vue';
import btsBtn from '@/components/common/button/btsBtn.vue';
import publishBtn from '@/components/common/button/publishBtn.vue';
import savePreviewBtn from '@/components/common/button/savePreviewBtn.vue';
import navigation from '@/components/mbbo/navigation/navigation.vue';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			business:  this.Static+'publish/purchase/business.png',
			contact:  this.Static+'publish/purchase/contact.png',
			store:  this.Static+'publish/purchase/store.png',
			loved: this.Static+'publish/purchase/stored.png',
			love: this.Static+'publish/purchase/store.png',
			againEdit:  this.Static+'publish/purchase/againEdit.png',
			publish:  this.Static+'publish/purchase/publish.pngg',
			product:  this.Static+'publish/purchase/product.png',
			isEdit:{
				isEditShowBtn: true,
				isBtnBottom: '88upx'
			},
			home: this.Static + 'mbcImg/tabBar/homed.png',
			routeParams: this.routeParam, // 
			quotedPriceShow: true, // 
			quotedPrice: {
			}, // 
			caiGouList: this.caiGouListData, // 
			businessTemLists: this.businessTemList,
			listData: null // 
		};
	},
	components: {
		navigation,
	},
	created () {
		this.caiGouList = this.GET_PUBLISH.publishPurchase.caiGouList;
		console.log(this.caiGouList, 'vuex');
		// this.quotedPrice = {
		// 	// day: this.returnDateNum( this.getNewDate(), this.caiGouList.purcEndTime), // 
		// 	day: this.caiGouList.day, // 
		// 	hour: this.caiGouList.hour || 0, //
		// 	minute: this.caiGouList.minute || 0, // 
		// 	second: 0, // 
		// 	quotedNum: 0, // 
		// 	startTime: this.getNewDate(), //
		// 	endTime: this.caiGouList.purcEndTime, //
		// };
		this.listData = this.GET_PUBLISH.publishPurchase.imgList;
		console.log(this.listData, '-------------++++++++++++------------------')
		console.log(this.returnDateNum( this.getNewDate(), this.caiGouList.purcEndTime));
		this.resetDataPage(this.routeParams);
		let purcCcodeStr = this.caiGouList.purcCcodeStr;
		console.log(purcCcodeStr);
		if (this.routeParams.sourceTemp !== 0) {
			console.log(purcCcodeStr.split('-'), '------------------+++++++++++++++')
			this.caiGouList.purcCcodeStr=  purcCcodeStr.split('-')[0] + '-' + purcCcodeStr.split('-')[1]
		}
	},
	filters:{
		switchText(val) {
			console.log(val, '-----------------shui--------------')
			if (Number(val) === 1) {
				return ''
			} else if (Number(val) === 0) {
				return ''
			};
		}
	},
	computed: {
		i18n() {
			return this.$t('qualificatCert');
		},
		...mapGetters(['GET_PUBLISH', 'QUICKNAVCO'])
	},
	props: [
		'routeParam',
		'caiGouListData',
		'businessTemList',
		'caiGouDetileData'
	],
	watch: {
		caiGouDetileData: {
			handler(a, b) {
				console.log(a, b, '---------------caiGouDetileData--------------');
				if (a) {
					if (a.isFollow === '1') {
						this.store = this.loved;
					} else {
						this.store = this.love;
					}
				}
			},
			deep: true
		},
		routeParam: {
			handler(a, b) {
				console.log(a, b, '---------------routeParam--------------');
				this.routeParams = a;
				this.resetDataPage(this.routeParams);
				let purcCcodeStr = this.caiGouList.purcCcodeStr;
				console.log(purcCcodeStr);
				if (this.routeParams.sourceTemp !== 0) {
					console.log(purcCcodeStr.split('-'), '------------------+++++++++++++++')
					this.caiGouList.purcCcodeStr=  purcCcodeStr.split('-')[0] + '-' + purcCcodeStr.split('-')[1]
				}
			},
			deep: true
		},
		businessTemList: {
			handler(a, b) {
				console.log(a, b, '---------------businessTemList--------------');
				this.businessTemLists = a.content;
			},
			deep: true
		},
		GET_PUBLISH: {
			handler(a, b) {
				console.log(a, b, '------------------GET_PUBLISH----------------');
				this.caiGouList = a.publishPurchase.caiGouList;
				this.quotedPrice = {
					day: this.caiGouList.day, // 
					hour: this.caiGouList.hour || 0, //
					minute: this.caiGouList.minute || 0, // 
					second: 0, // 
					quotedNum: 0, // 
					startTime: this.getNewDate(), //
					endTime: this.caiGouList.purcEndTime, //
				};
				this.quotedPriceShow = false;
				this.$nextTick(function() {
					this.quotedPriceShow = true
				});
				this.listData = a.publishPurchase.imgList;
			},
			deep: true
		}
	},
	mounted() {
		console.log(this.GET_PUBLISH.publishPurchase,'publishPurchase.caiGouList')
	},
	beforeDestroy () {
		console.log('-----------setImgListsetCaiGouList');
		// this.$store.commit('setImgList', this.listData); // ssetImgList
		// this.$store.commit('setCaiGouList', this.caiGouList); // setCaiGouList
	},
	components: {
		topBox,
		uniCountDown,
		fixedBgBtn,
		btsBtn,
		publishBtn,
		savePreviewBtn
	},
	methods: {
		...mapMutations({
			setPreviceCaiGou: 'setPreviceCaiGou'
		}),
		previewImageS (e, key) {
			console.log(e, '');
			let imgList = [];
			e.map((items, index) => {
				imgList.push(items.imgUrl)
			});
			uni.previewImage({
				current: imgList[key],
				indicator: "number",
				loop: "true",
				urls:imgList
			})
		},
		caiGouSavePublish (e) {
			console.log(this.GET_PUBLISH.publishPurchase, '');
			let params = this.GET_PUBLISH.publishPurchase.caiGouList;
			let listData = this.GET_PUBLISH.publishPurchase.imgList.imageData;
			console.log(listData, '---------------listData------------')
			if (params.purcTitle === '') {
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 1000
				});
				return
			}
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 
				console.log(landRegistLG.user.id);
				uni.showLoading({
					// loading
					title: ''
				});
				params.purcState = e; // 
				if (listData) { // 
					console.log(listData, '')
					listData.map((items, index) => {
						if (items.imgName) {
							params.imgList.push(items.imgName)
						}
					})
				};
				params.userId = landRegistLG.user.id; // id
				params.purcType = String(this.GET_PUBLISH.publishPurchase.templetType);//  // 
				delete params.purcCcodeStr; // text
				params.day ? delete params.day : console.log('');
				params.hour ? delete params.hour : console.log('');
				params.minute ? delete params.minute : console.log('');
				params.purcReceTime === '0-0-0' ? params.purcReceTime = '' : console.log('');
				let url = '/rest-rp/purc/add';
				if (this.routeParam.sourceTemp === 4) { // 
					url = '/rest-rp/purc/update';
				};
				if (params.purcType === '0') { // 
					params.purcEndTime = '';
					params.purcInvo = '0';
					params.purcFrei = '0';
				};
				uni.request({
					url: this.api2 + url, //
					data: params,
					method: 'POST',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //token
					},
					success: response => {
						if (response.data.ret === '200') {
							uni.hideLoading(); //  loading
							if (e === '0') { // 
								uni.showToast({
									title: '',
									icon: 'none',
									duration: 1000
								});
								console.log(response.data.content);
								uni.navigateTo({
									url: '/modules/publishPurchase/selectTemplate/publishPurchaseFinish/publishPurchaseFinishShare?urlParams=' + this.urlCrypto(response.data.content, 0)
								});
							} else if (e === '1') { // 
								uni.showToast({
									title: '',
									icon: 'none',
									duration: 1000
								});
								console.log(response.data.content);
								uni.navigateTo({
									url: '/modules/publishPurchase/selectTemplate/publishPurchaseFinish/publishPurchaseFinishShare?urlParams=' + this.urlCrypto(response.data.content, 0)
								});
							}
						} else {
							uni.hideLoading(); //  loading
							uni.showToast({
								title: '',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); //  loading
						uni.showToast({
							title: '',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '');
					}
				});
			}
		},
		tapStorePreview() {
			console.log(this.businessTemList.content, '');
			uni.navigateTo({
				url: '/modules/myCompany/mybusiness/my-business?id=' + this.routeParam.creatorId
			});
		},
		tapContactPreview() {
			console.log(this.businessTemList.content, '');
		
			if (this.routeParam.sourceTemp === 2 || this.routeParam.sourceTemp === 3) {
				// 
				let parmas = {
					userId: this.routeParam.lookUserId,
					id: this.routeParam.purcId,
					type: 4
				};
				this.platformAccount(this.routeParam.isFlow, 8, parmas, this.routeParam).then(res => {
					console.log(res);
				})
				.catch(err => {
					console.log(err);
				});
			};
			uni.makePhoneCall({
				phoneNumber: this.businessTemList.content.contactTelphone // 
			});
		},
		tapCollectPreview() {
			let parmas = {
				userId: this.routeParam.lookUserId,
				modelId: this.routeParam.purcId,
				type: 1
			};
			this.focusStore(parmas).then(res => {
				console.log(res);
				if (res.success === true) {
					this.store = this.loved;
				} else {
					this.store = this.love;
				}
			})
			.catch(err => {
				console.log(err);
			});
		},
		myQuote () {
			uni.showToast({
				title: '',
				icon: 'none',
				duration: 1000
			});
		},
		mySeek () {
			uni.showToast({
				title: '',
				icon: 'none',
				duration: 1000
			});
		},
		resetDataPage(e){
			console.log(e, '------------');
			if (e.sourceTemp === 0) { // 
				this.isEdit.isEditShowBtn = true;
				this.isEdit.isBtnBottom = '88upx';
			} else if (e.sourceTemp !== 0) {
				if (e.sourceTemp === 4) { // 
					if (e.purcState === '0' || e.purcState === '4') { // 
						this.isEdit.isEditShowBtn = true;
						this.isEdit.isBtnBottom = '88upx';
					} else { // 
						this.isEdit.isEditShowBtn = false;
						this.isEdit.isBtnBottom = '0upx';
					}
				} else if (e.sourceTemp !== 4) { // 
					this.isEdit.isEditShowBtn = false;
					this.isEdit.isBtnBottom = '0upx';
				}
			}
		},
		getNewDate () {
			console.log('');
			var day2 = new Date();
			day2.setTime(day2.getTime());
			var s2 = day2.getFullYear()+"-" + (day2.getMonth()+1) + "-" + day2.getDate();
			return s2
		},
		againEditCaigou () {
			console.log('');
			this.$store.commit('setPreviceCaiGou', false); // setPreviceCaiGou
		}
	}
};
